<template>
	<section class="formulare">
		<div class="row">
			<div class="col-md-12">

				<!--Title-->
				<div class="row">
					<div class="col-md-12 text-center">
						<h2>
							Našli sme <span class="text-orange" id="productCount">{{ products_found }}</span>
						</h2>
						<div class="results-send-export">
							<a class="link" href="#" data-toggle="modal" data-target="#email-modal">
								<span class="i_email"></span>poslať e-mailom
							</a>
							<a class="link" id="offerDownload" href="#">
								<span id="offerDownload_icon" class="i_download"></span>stiahnuť pdf
							</a>
						</div>
					</div>
				</div>

				<!--Suhrn udajov-->
				<div class="row form-summary py-3 mb-1">
					<div class="col-10 pr-1" id="vase_udaje">
						<strong class="strong">Vaše údaje:</strong>
						<span products-udaje="">
							{{ user_data }}
						</span>
					</div>
					<div class="col-2 text-right my-auto">
						<a id="step2_back_button" class="link text-nowrap" href="#">
							<span class="i_edit"></span>
							<span class="d-none d-md-inline" @click="go_back()">upraviť</span>
						</a>
					</div>
				</div>

				<!--Formular (vsetky inputy ktoré treba)-->
				<form id="globalForm" class="row results-filter py-3 hidden">
					<!-- inserted via js -->
				</form>

				<!-- filters - html and styles are temporary -->

				<!-- Vrchny loading, zmizne ked sa nacitaju vsetky produkty -->
				<div v-show="loaders_active > 0">
					<div class="spinner">
						<div class="bounce1"></div>
						<div class="bounce2"></div>
						<div class="bounce3"></div>
					</div>
				</div>

				<!-- Vypis vsetkych produktov -->
				<div id="productResults">
					<Product_template v-for="(value, key) in products" :product_name="value" :product_id="key" @calculate_done="calculate_done" />
				</div>

				<!-- Spodny loading, zobrazi sa ked sa nacita prvy produkt, zmizne ked sa nacitaju vsetky produkty -->
				<div v-show="loaders_active > 1">
					<div class="spinner">
						<div class="bounce1"></div>
						<div class="bounce2"></div>
						<div class="bounce3"></div>
					</div>
				</div>

			</div>
		</div>
	</section>
</template>

<script>
	import State from '@/store/State.js';
	import Product_template from '@/components/form_app/product/Product_template.vue';
	import Product_requests from '@/logic/requests/Product_requests.js';

	export default {
		props: {
		},
		components: {
			Product_template,
		},
		data() {
			return {
				loaded: 0,
				loaders_active: 1, // number of loaders displayed
				user_data: '',
				products: {},
			};
		},
		created() {
			Product_requests.get_product_list().then(
				(response) => {
					this.products = response.result.products;
				}
			);
		},
		methods: {
			go_back() {
				State.change_step(1);
			},
			calculate_done(udaje) {
				this.loaded++;
				this.user_data = udaje;
				if (this.loaded === Object.keys(this.products).length) {
					this.loaders_active = 0;
				} else {
					this.loaders_active = 2;
				}
			},
		},
		computed: {
			products_found() {
				if (this.loaded === 1) {
					return this.loaded + ' produkt';
				} else if (this.loaded > 1 && this.loaded < 5) {
					return this.loaded + ' produkty';
				} else /* 0 or 5+ */ {
					return this.loaded + ' produktov';
				}
			},
		},
	}
</script>
